import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, Platform, AlertController } from 'ionic-angular';
import { AuthService } from '../../providers/auth-service';
import { LoginPage } from '../login/login';

/**
 * Generated class for the IdfindresultPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-idfindresult',
  templateUrl: 'idfindresult.html',
})
export class IdfindresultPage {
  credentials: any;
  result: any;

  constructor(
      public navCtrl: NavController, 
      public navParams: NavParams,
      public viewCtrl: ViewController,
      public alertCtrl: AlertController,
      public platform : Platform,
      private auth : AuthService,
    ) {
  }

  async ionViewDidLoad() {
    if(this.navParams.get('credentials')) {
      this.credentials = await this.navParams.get('credentials');
    }

    if(this.credentials) {
      this.auth.idFind(this.credentials).subscribe(data => {
        this.result = data.id;
      }, error => {
        this.showErrorAlert();
      })
    }

    if(this.result !=='') {
      console.log(" ");
    }
    
    console.log('ionViewDidLoad IdfindresultPage');
  }

  ionViewDidEnter(){
    console.log('ionViewDidEnter IdfindresultPage');
  }

  ionViewWillEnter(){
    console.log('ionViewWillEnter IdfindresultPage');
  }

  close() {
    this.viewCtrl.dismiss();
  }

  //   2020-06-02
  emailSecurity(userEmail){
    varid = userEmail.split('@')[0];
    varmail = userEmail.split('@')[1];
    varmaskingId =function(id){
      varsplitId = id.substring(0,4);
      for(vari =1; i < id.length; i++){ 
        splitId +='*'; 
      }
      returnsplitId; 
    };
    varmaskingMail =function(mail){
      varsplitMail ='';
      for(vari =1; i < mail.length; i++){ 
        splitMail +='*'; 
      } splitMail += mail.substring(mail.length-1,mail.length);
      returnsplitMail; 
    }; 
    userEmail = maskingId(id) +'@'+ (mail);
    returnuserEmail; 
  }

  showErrorAlert(){
    let alert2 = this.alertCtrl.create({
      cssClass: 'push_alert',
      title: '',
      message: "  ",
      buttons: [
        {
          text: '',
          handler: () => {
            this.viewCtrl.dismiss();
          }
        }
      ]
    });
    alert2.present();
  }

  setHome() {
    this.navCtrl.setRoot(LoginPage);
  }
}



